
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import myReportService from '../service/myReportService'
import { setTimeout } from 'timers';
import loading from './loading';
const list = () => ['本日', '本周', '本月', '本年']
export default {
components:{
Tab,
TabItem,
Divider,
loading,
SwiperItem,
FormPreview,
CellFormPreview
},
data(){
return{
listTab: list(),
demo2: '本日',
list1:[],
list2:[],
list3:[],
list1Total:0,
list2Total:0,
list3Total:0,
remark:'',
spinner:false
}
},
filters:{
numberComma
},
methods:{
mylist(tab){
if(tab == 0){
this.listpanl(this.remark.days)
}else if(tab == 1){
this.listpanl(this.remark.weeks)
}else if(tab == 2){
this.listpanl(this.remark.months)
}else if(tab == 3){
this.listpanl(this.remark.years)
}
},
listData(){
let ROSE_OPTION=JSON.parse(localStorage.getItem('ROSE_OPTION'));
let jsonData={
shengName:ROSE_OPTION.region,
bankName:ROSE_OPTION.bank,
sybName:ROSE_OPTION.dept,
bmName:ROSE_OPTION.captain,
}
myReportService.myRepotList(jsonData).then(data=>{
this.spinner = true;
if(data){
this.spinner = false;
}
this.remark = data;
this.mylist(0);
}).catch(data=>{
this.spinner = true;
})
},
listpanl(m){
this.list1Total = 0;
this.list2Total = 0;
this.list3Total = 0;
this.list1.length = 0;

for(let i = 0; i<m.length; i++){

if(m[i].objType == '项目类产品' && m[i].objName){
this.list1Total += Number(m[i].amount);
this.list1.push({
label:m[i].objName,
value:'￥'+numberComma(m[i].amount,3)+' '+'('+m[i].qty+'件/套'+')',
})
}else if(m[i].objType == 'A'){
this.list2Total = m[i].amount
}else if(m[i].objType == 'B'){
this.list3Total = m[i].amount
}
}
this.list1Total=(this.list1Total).toFixed(2);
}
},
mounted(){
this.listData();
}
}



